// RESET
h1,h2,h3,h4,h5,h6,p {font-size:16px; line-height:16px;}



// GENERAL STYLES
html {font-size: 16px;}
body {font-family:'Poppins',sans-serif; color:#484848;}
h1 {font-size:2rem; font-weight:700; line-height:2rem; margin:0; text-align:right; text-transform:uppercase;}
h2 {font-size:1.2rem; line-height:1.6rem; font-weight:200; letter-spacing:0.03rem; text-align:right;}
h3 {font-size:1.14rem; font-weight: 700;}
p {font-size:1rem; font-weight:200; line-height:1.6rem;}
ul {list-style-type:none; margin:0;}
// /GENERAL STYLES

// SOBRESCRIBE
a:visited {color:unset;}
a:-webkit-any-link {color:unset;}
.page {margin:0;}
// /SOBRESCRIBE

// MAIN STYLE
.container {display:grid;}


// PRESENTATION left side
.presentation {background-color:#fafafa; position:sticky; top:0; height:100vh; box-sizing:border-box; display:flex; flex-direction:column; align-items:flex-end; padding:5% 5%;}
.presentation__central {align-items:flex-end; display:flex; flex-direction:column; flex:1; justify-content:center; margin-bottom:-10%;}
.presentation__icons {color: #48484870; margin:1rem 0 1rem 0;}
.presentation__icons--item {font-size: 1.3rem; float:left; margin:0 0 0 1.6rem;}
.presentation__icons--item::before {display:inline-block; font-style:normal; font-variant:normal; text-rendering:auto; -webkit-font-smoothing:antialiased;}
// /PRESENTATION left side

// PORTFOLIO right side
.portfolio {background-color:#fafafa; display:grid;}

.project-full {display:grid; position:relative;}
.project-full img {align-self:center; justify-self:center;}

.split-article {display:grid; grid-template-columns: repeat(2, 1fr); height:50vh; position:relative;}
.project-half {display:grid;}
.project-half__img {align-self:center; justify-self:center;}
    
.project__img--contain {object-fit: contain; object-position: center; width: 80%; height: 80%;}
.project__img--cover {height: 100%; object-fit: cover;}
.project__caption--up {position: absolute; top: 5%; left: 5%;}
.project__caption--down {bottom:5%; font-size:0.875rem; opacity:0.4 !important; position:absolute; right:5%;}
.project__caption--down p {font-size:1rem; margin:0;}
.project__caption--white {color: white;}
.project__caption--black {color: #484848;}
.project__number, .project__company, .project__type, .project__kit {margin: 0;}
.project__name {margin: 1rem 0;}
.project__link {display: inline-block; font-size: 1rem; margin-right: 1rem; vertical-align: middle;}
.project__links {margin: 1rem 0;}
.project__kit {margin:0; padding:0;}
.project__kit--item {float:left; margin: 0 1.2rem 0 0;}
// /PORTFOLIO right side

// /MAIN STYLE



// MEDIA QUERIES
@media (min-width: 300px) { 
    .container {display:grid; grid-template-columns:1fr;}

    .presentation {z-index: unset;}

    .project-full{height: 100vh;}
    .split-article {display:grid; grid-template-columns: 1fr; height:100vh; position:relative;}

    .project__img--contain {height:90%; object-fit:contain; object-position:center; width:90%;}
}
@media (min-width: 576px) {
}

@media (min-width: 768px) {
}

@media (min-width: 992px) { 
    .container {display:grid; grid-template-columns:45% 55%;}

    .presentation {box-shadow: rgba(0, 0, 0, 0.06) 10px 0px 10px 0px; z-index: 1;}

    .project-full{height: 70vh;}
    .split-article {display:grid; grid-template-columns: repeat(2, 1fr); height:50vh; position:relative;}

    h1 {font-size:2.6rem; line-height:2.6rem;}
} 
@media (min-width: 1200px) {
    .container { display: grid; grid-template-columns: 40% 60%;}
}
// /MEDIA QUERIES

// SOCIAL ICONS
.fa-dribbble, .fa-behance, .fa-github, .fa-linkedin-in, .fa-codepen {display: inline-block;transition: all .2s ease-out;}
.fa-dribbble:hover, .fa-behance:hover, .fa-github:hover, .fa-linkedin-in:hover, .fa-codepen:hover {transform: translateY(-3px);}
.fa-dribbble:hover {color: #ea4c89;}
.fa-behance:hover {color: #1769ff;}
.fa-github:hover {color: #333;}
.fa-linkedin-in:hover {color: #0077b5;}
.fa-codepen:hover { color: rgb(189, 189, 189); }
// /SOCIAL ICONS




